import { styled } from '@mui/system';
import useMediaQuery from '@mui/material/useMediaQuery';
import Typography from '@mui/material/Typography';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import { useParallax } from 'react-scroll-parallax';

import logo from './logo2.svg';

export default function Banner() {
  const isDesktop = useMediaQuery('(min-width:600px)');
  const logoParallax = useParallax({
    easing: 'easeOutQuad',
    translateX: [-16, 0],
    translateY: [-64, 0],
    rotate: [-15, 15],
  });
  const textParallax = useParallax({
    easing: 'easeIn',
    translateY: [-16, 0],
    opacity: [0, 1],
  });
  const StyledComponent = styled('div')({
    textAlign: 'center',
    height: (window.innerHeight + 128) + 'px',
    background: 'linear-gradient(black 50%, #17293a)',
  });
  return (
    <div>
      <StyledComponent>
        <Typography variant={isDesktop ? 'h1' : 'h4'} component={isDesktop ? 'h2' : 'h5'} 
          style={{position: 'relative', top: (window.innerHeight/2-100) + 'px'}}>
          Coming soon...
        </Typography>
        <ArrowCircleDownIcon 
          style={{
            position: 'relative',
            top: (window.innerHeight - 256) + 'px',
            color: 'gray',
          }} />
      </StyledComponent>
      <div ref={logoParallax.ref} style={{
        marginTop: (window.innerHeight/2) + 'px',
      }}>
        <img src={logo} className="App-logo" alt="logo" style={{height: '40vmin'}}/>
      </div>
      <div ref={textParallax.ref}>
        <Typography variant={isDesktop ? 'h1' : 'h4'} component={isDesktop ? 'h2' : 'h5'}>
          Spring Camp 2023
        </Typography>
      </div>
    </div>
  );
}